<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                技术需求
                <img class="search" src="../../assets/technology/icon_search@2x.png" alt="" @click="goSearch">
            </p>
        </section>
        <section class="middle">
            <md-drop-menu class="area-menu" ref="menu" :data="MenuData" @change="changeMenu"/>
            <div class="area-btn" @click="showMenu">
                <span>地区选择</span>
            </div>
        </section>
        <section class="bottom">
            <div class="md-example-child md-example-child-scroll-view md-example-child-scroll-view-0" :style="{height: Height}">
                <md-scroll-view @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                    <div
                            v-for="i in list"
                            class="scroll-view-item"
                            :key="i.id"
                            @click="$_onItemClick(i)">
                        <label>{{ i.genre }}</label>
                        <img :src="i.img" alt="">
                        <div class="article">
                            <p v-text="i.name"></p>
                            <span v-text="i.unitName"><img src="@/assets/home/icon_time@2x.png" alt=""></span>
                        </div>
                    </div>
                    <md-scroll-view-more slot="more" :is-finished="isFinished">
                    </md-scroll-view-more>
                </md-scroll-view>
                <md-tab-picker
                        title="所在地区"
                        describe=""
                        large-radius
                        :data="areaData"
                        v-model="show"
                        v-if="show"
                        @select="handleSelect"
                        @change="handleChange"/>
            </div>
        </section>
    </main>
</template>

<script>
import { getList } from '@/apiUrl/technology'
import { getDictionary } from '@/apiUrl/login'
import { getCity, getArea } from '@/apiUrl/base'
import areaData from '../../../static/data'
import { TabBar, Swiper, SwiperItem, ScrollView, ScrollViewMore, DropMenu, Icon, Toast, TabPicker } from 'mand-mobile'
export default {
  name: 'Devices',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [ScrollViewMore.name]: ScrollViewMore,
    [DropMenu.name]: DropMenu,
    [Icon.name]: Icon,
    [Toast.name]: Toast,
    [TabPicker.name]: TabPicker
  },
  data () {
    return {
      searchParams: {
        pageSize: 10,
        pageNumber: 1,
        genre: '',
        province: '',
        city: '',
        area: ''
      },
      // tab项
      list: 10,
      isFinished: false,
      Height: '900px',
      MenuData: [
        {
          text: '所属领域',
          options: []
        }
      ],
      show: false,
      areaData: areaData,
      provinceId: '',
      cityId: ''
    }
  },
  mounted () {
    this.getList()
    this.getGenre()
    // this.getProvince()
    this.Height = window.innerHeight - 75 + 'px'
  },
  methods: {
    getList () {
      getList(this.searchParams).then(res => {
        this.list = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    // 获取所属领域列表
    getGenre () {
      getDictionary({ type: 'genre' }).then(res => {
        res.data.forEach(item => {
          item.text = item.label
        })
          const args = {
              description: "领域",
              id: "",
              img: "",
              label: "全部",
              text: '全部',
              remarks: "4",
              sort: 1,
              type: "genre",
              value: ""
          }
          res.data.unshift(args)
        this.MenuData[0]['options'] = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    getCity () {
      getCity().then().catch()
    },
    getArea () {
      getArea().then().catch()
    },
    getSelectedValue (index) {
      console.log(index)
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push({
        path: '/technology/detail',
        query: { id: i.id }
      })
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.list = this.list.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    // 回退
    goBack () {
      this.$router.go(-1)
    },
    // 跳转关键字检索页
    goSearch () {
      this.$router.push('/search/index')
    },
    changeMenu (val) {
      this.searchParams.genre = this.$refs.menu.getSelectedValue(0).value
      this.searchParams.pageNumber = 1
      this.getList()
    },
    showMenu (val) {
      this.show = true
    },
    handleChange (val) {
      val.values.forEach((item, key) => {
        console.log(item)
        switch (key) {
          case 0: this.searchParams.province = item; break
          case 1: this.searchParams.city = item; break
          case 2: this.searchParams.area = item; break
        }
      })
      this.searchParams.pageNumber = 1
      this.getList()
    },
    handleSelect (data) {
      console.log('run', data)
      if (data.option.name === 'province') {
        this.areaData = areaData
        this.provinceId = data.option.selected.index
        getCity({ provinceid: data.option.selected.id }).then(res => {
          this.areaData.options[this.provinceId].children.options = []
          res.data.forEach((item, key) => {
            this.areaData.options[this.provinceId].children.options.push({
              label: item.city,
              id: item.cityid,
              value: item.city,
              index: key,
              children: {
                'name': 'area',
                'label': '请选择',
                'options': [{
                  'value': '',
                  'label': '',
                  'index': 2
                }]
              }
            })
          })
          // this.areaData.options[this.provinceId].children.options.shift(0)
        }).catch(err => {
          Toast.failed(err)
        })
      } else if (data.option.name === 'city') {
        console.log(data)
        if (data.option.selected) {
          this.cityId = data.option.selected.index
        }
        if (!(data.option.selected && data.option.selected.hasOwnProperty('id'))) {
          return
        }
        getArea({ cityid: data.option.selected.id }).then(res => {
          this.areaData.options[this.provinceId].children.options[this.cityId].children.options = []
          res.data.forEach(item => {
            this.areaData.options[this.provinceId].children.options[this.cityId].children.options.push({
              label: item.area,
              id: item.areaid,
              value: item.area
            })
          })
          // this.areaData.options[this.provinceId].children.options[this.cityId].children.options.shift(0)
        }).catch(err => {
          Toast.failed(err)
        })
      }
      // this.show = true
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        padding-top 88px
        height 100%
        margin-bottom 0
        .middle
            height 80px
            .md-drop-menu
                width 50%
                height 80px
                border-bottom 1px solid #eeeeee
                .md-drop-menu-bar
                .bar-item
                    border-right 1px solid #eeeeee
            .area-btn
                width 50%
                height 80px
                line-height 80px
                border-bottom 1px solid #eeeeee
                font-size 28px
                color #666666FF
                position fixed
                right 0
        .bottom
            width 690px
            margin 0 auto
    .md-example-child-scroll-view-0
        height 100%
        background #FFF
        .scroll-view-item
            text-align center
            font-size 28px
            font-family DINAlternate-Bold
            height 180px
            margin-bottom 30px
            border-radius 8px
            border 2px solid rgba(2,25,85,0.11);
            label
                position absolute
                color #ffffff
                background:linear-gradient(0deg,rgba(202,2,57,1),rgba(255,97,53,1))
                height 32px
                line-height 32px
                left 8px
                font-size 20px
                font-family PingFang-SC-Regular
                border-radius 0 0 4px 4px
    .scroll-view-container
        div
            display flex
            justify-content space-between
            img
                width 260px
                height 100%
            .article
                width 390px
                text-align left
                margin 25px 0
                display flex
                flex-direction column
                justify-content space-between
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                    text-overflow ellipsis
                    overflow hidden
                    display -webkit-box
                    -webkit-line-clamp 2
                    -webkit-box-orient vertical
                span
                    display flex
                    align-items center
                    line-height 24px
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
        .md-scroll-view-more
            justify-content center
    .md-drop-menu
        font-size 28px
        top 88px
        z-index 8
    /deep/ .md-drop-menu-list
        padding-top 170px!important
    /deep/.scroll-view-container
        margin-top 36px
</style>
