import request from '../utils/request'
import apiPath from './url'
import qs from 'qs'

export function getProvince (data) {
  data = qs.stringify(data)
  return request({
    url: apiPath.base.province,
    method: 'post',
    data: data
  })
}
export function getCity (data) {
  data = qs.stringify(data)
  return request({
    url: apiPath.base.city,
    method: 'post',
    data: data
  })
}
export function getArea (data) {
  data = qs.stringify(data)
  return request({
    url: apiPath.base.area,
    method: 'post',
    data: data
  })
}
